import React from "react";

import "./Heraldry.css";

function Heraldry(props) {
  const { name } = props;

  return (
    <figure className="Heraldry">
      <img
        width={"100%"}
        className="HeraldryImage"
        alt={`${name} heraldry`}
        src={`/images/${name}.png`}
      />
      <figcaption>{name}</figcaption>
    </figure>
  );
}

export default React.memo(Heraldry);
